/* Reset + base styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    background-color: black;
    font-family: 'Courier New', monospace;
    color: white;
    overflow-x: hidden;
}

/* App root layout */
.App {
    background-color: black;
    min-height: 100vh;
    padding-bottom: 2rem;
}

/* CRT shader effect */
.crt-screen {
    background-color: black;
    filter: contrast(1.1) brightness(1.05);
    position: relative;
}

    .crt-screen::before {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: repeating-linear-gradient( rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.02) 1px, transparent 1px, transparent 2px );
        z-index: 1;
        pointer-events: none;
    }

/* Pixel Font */
.pixel-font {
    font-family: 'Courier New', Courier, monospace;
    letter-spacing: 1.5px;
}

/* Loading Screen */
.loading-screen {
    background-color: black;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #9c6bff;
    font-size: 2rem;
}

.progress-bar {
    width: 200px;
    height: 10px;
    background-color: #333;
    margin-top: 1rem;
    border-radius: 5px;
    overflow: hidden;
}

.bar-fill {
    width: 100%;
    height: 100%;
    background-color: #9c6bff;
    animation: loading 2s linear forwards;
}

@keyframes loading {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

/* Header */
.header {
    text-align: center;
    padding: 2rem 1rem;
    background-color: black;
}

    .header nav a {
        color: #9c6bff;
        margin: 0 1.2rem;
        text-decoration: none;
        font-size: 1.1rem;
    }

        .header nav a:hover {
            text-decoration: underline;
        }

/* Sections */
section {
    padding: 3rem 1rem;
    max-width: 900px;
    margin: auto;
}

/* Projects Section */
.projects .project-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.project-card {
    background-color: #1a1a1a;
    padding: 1rem;
    border-radius: 8px;
    border: 1px solid #333;
    text-align: center;
}

    .project-card img {
        width: 100%;
        height: auto;
        border-radius: 4px;
        margin-bottom: 0.5rem;
    }

    .project-card h3 {
        color: #9c6bff;
    }

    .project-card .links a {
        margin: 0 0.5rem;
        color: #ffcc00;
        text-decoration: none;
    }

        .project-card .links a:hover {
            text-decoration: underline;
        }

/* Contact Section */
.contact a {
    color: #ffcc00;
    text-decoration: none;
}

    .contact a:hover {
        text-decoration: underline;
    }

/* Social Icons */
.contact-social {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

    .contact-social a {
        font-size: 2rem;
        color: #9c6bff;
    }

        .contact-social a:hover {
            color: #ffcc00;
        }

/* Footer */
.footer {
    text-align: center;
    padding: 1.5rem 1rem;
    font-size: 0.9rem;
    background-color: black;
    color: #aaa;
}